import {
  getStrapiMedia
} from '~/utils/medias'

export default {
  data() {
    return {
      product: null,
      error: null
    }
  },
  async mounted() {
    try {
      this.product = await this.$strapi.$products.findOne(this.$route.params.id)
    } catch (error) {
      this.error = error
    }
  },
  computed: {
    customFields() {
      return this.product["Custom_field"]
        .map(({
          title,
          required,
          options
        }) => ({
          name: title,
          required,
          options
        }))
        .map((x, index) => Object.entries(x)
          .map(([key, value]) => ({
            [`data-item-custom${index + 1}-${key.toString().toLowerCase()}`]: value
          })))
        .reduce((acc, curr) => acc.concat(curr), [])
        .reduce((acc, curr) => ({
          ...acc,
          ...curr
        }))
    }
  },
  methods: {
    getStrapiMedia
  }
}
